import { Component, OnInit, Output, EventEmitter} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent implements OnInit {
  @Output() _dir: EventEmitter<string> = new EventEmitter<string>();
  @Output() loading: EventEmitter<boolean> = new EventEmitter<boolean>();

  dir = localStorage.getItem('dir') || 'ltr';
  rtl: boolean = this.dir == 'rtl' ? true : false;
  lang: any = '';
  Margin: any = '';
  constructor(private translate: TranslateService) {
    this.lang = localStorage.getItem('dir') == 'rtl' ? 'ar' : 'en';
    this.translate.use(this.lang);

  }

  ngOnInit(): void {
    let ele = document.getElementById("nav");
    let navH = ele?.clientHeight;
    this.Margin = navH;

  }
  useLanguage(lan: any) {
    if (lan == 'ar') {
      this.dir = "rtl";
      this.rtl = true;
      localStorage.setItem('dir', 'rtl');
    } else {
      this.dir = "ltr";
      this.rtl = false;
      localStorage.setItem('dir', 'ltr');

    }
    this.lang = lan;
    this.translate.use(lan);
    this._dir.emit(this.dir);


  }
}
